header {
  padding: 1.5rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  max-width: 70rem;
  margin-inline: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 7;
  transition: color .5s, background-color .5s, filter .5s;
}

header>span {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.5rem;
}

header.scrolled {
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
}

header.scrolled img {
  filter: invert();
}

header a {
  text-decoration: none;
  color: inherit;
}

header a:visited {
  text-decoration: none;
  color: inherit;
}

header nav {
  display: flex;
}

header nav a {
  padding: 5px;
}

header nav a.active,
header .menu a.active {
  background-color: grey;
}

header .menu {
  visibility: hidden;
  display: none;
}


@media (max-width: 500px) {
  header {
    padding: 12px 24px;
  }

  header>nav {
    display: none;
  }

  header .menu ul {
    visibility: hidden;
    position: absolute;
    transform: translateY(-100%);
    transition: transform .5s ease-in-out;
  }

  header>.menu {
    display: inline-block;
    visibility: visible;
    position: relative;
    z-index: 7;
  }

  .menu li {
    color: white;
    list-style: none;
    padding: 5px;
  }

  header .menu ul.active {
    transform: none;
    padding: 0;
    margin: 0;
    display: flex;
    visibility: visible;
    flex-flow: column nowrap;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 0;
  }
}